.app {
    display: flex;
}

.right_side {
    flex: 1;
    padding-left: 360px;
    background-color: #fff;
}

.right_side .right_nav_side {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 16px 0 16px 200px;
    border-bottom: 1px solid #eee;
    .search_wrap{
        width:20vw;
        input {
            width: 250px;
            padding: 6px 10px;
            border: 1px solid #ccc;
            border-radius: 3px;
        }
    }
}


.right_side .route_views {
    width: 65%;
    padding: 30px 0 60px 200px;
    box-sizing: border-box;
}

.right_side .quick_way {
    width: 20%;
    height: 100px;
    padding: 0 20px;
    box-sizing: border-box;
    position: fixed;
    top: 100px;
    right: 0;
    border-left: 1px solid #eee;
}